*,
*:before,
*:after {
  -webkit-box-sizing: border-box !important;
  -moz-box-sizing: border-box !important;
  -ms-box-sizing: border-box !important;
  box-sizing: border-box !important;
}

.wrapper {
  background: #ffffff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1160px;
  margin: 0 auto;
  box-sizing: border-box;
  font-size: 14px;
  line-height: 1.2;
  color: #000000;
  font-family: "Inter";
	margin: 0 auto;
}
.mainBtn {
  width: 245px;
  height: 55px;
  background: #9dd458;
  border-radius: 18px;
  border: 0;
  color: #fff;
  font-size: 16px;
  
}
